#addbutton {
  background-color: chocolate;
  color: white;
  padding: 20px;
  font-size: 20px;
}
#inputbox {
  background-color: rgb(109, 6, 141);
  color: white;
  padding: 20px;
  font-size: 20px;
}
#inputbox::placeholder {
  color: white;
}
.listbox {
  border: 2px solid white;
  background-color: aliceblue;
  min-height: 200px;
}

.listitem {
  border: 2px solid coral;
  border-radius: 5px;
  position: relative;
  margin: 3px;
  font-size: 20px;
}
.delete-button {
  color: white;
  background-color: red;
  border-radius: 5px;
  position: absolute;
  right: 3px;
}
